@import "../mixins";

.quiz {
    padding: var(--big-offset) 0;
    &__content {
      border-radius: 4px;
      padding: var(--main-offset) 100px;
      background-color: var(--color-bg);

      @include small-tablet {
          padding: var(--main-offset);
      }
    }

    &__title {
        margin-bottom: var(--second-offset);
    }

    &__descr {
        margin: 0;
        border-bottom:  1px solid currentColor;
        padding-bottom: var(--small-offset);
        font: var(--middle-book);
        color: var(--color-jumbo);

    }
}

.quiz-questions {
padding: var(--second-offset) 0;
}

.quiz-question {
    &__title {
        margin: 0;
        margin-bottom: var(--second-offset);
        font: var(--prehigh-book);
        color: var(--color-main);
    }

    &__label {
        display: inline-block;
        @include mobile {
            width: 100%;
        }
    }

    &__message {
        border: none;
        resize: none;
        border-radius: 4px;
        padding: var(--second-offset);
        width: 465px;
        height: 246px;
        background-color: var(--color-white);
        color: var(--color-cameo);

        @include mobile {
            width: 100%;
        }
      
    }

    &__answers {
        display: grid;
        grid-auto-rows: 155px;
        grid-template-columns: repeat(3, 1fr);
        gap: var(--second-offset) 70px; 
        padding-bottom: var(--main-offset);

        @include small-tablet {
         gap: var(--second-offset); 
        }

        @include mobile {
            display: block;
        }
       
    }

    &__item {
        img {
            margin-bottom: var(--small-offset);
        }

        @include mobile {
           &:not(:last-child) {
               margin-bottom: 30px;
           }
        }
    }

   

    &__count {
        font: var(--middle-book);
        color: var(--color-jumbo);
    }
}

.quiz-bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: var(--second-offset);
    border-top: 1px solid var(--color-jumbo);

    @include mobile {
        display: block;
    }
}

.last-question {
    display: none;
    padding-top: var(--second-offset);

  &__form {
      position: relative;
      background: var(--color-cameo);
      padding: var(--main-offset);
      max-width: 655px;
      border-radius: 4px;
  }

  &__decorate {
      position: absolute;
      top: 0px;
      right: -180px;

      @include small-tablet {
          display: none;
      }
  }

    &__title {
        margin: 0;
        margin-bottom: var(--small-offset);
        font-weight: 700;
        font-size: 36px;
        line-height: 140%;
        color: var(--color-white);

        @include mobile {
            font-size: 24px;
            line-height: 30px;
        }
    }

    &__descr {
        margin: 0;
        margin-bottom: var(--second-offset);
        font-weight: 400;
        font-size: 18px;
        line-height: 140%;
        color: var(--color-white);
    }

    &__input {
        margin-bottom: var(--small-offset);
        max-width: 500px;

        &:nth-of-type(2){
            margin-bottom: var(--second-offset);
        }

         @include mobile {
            max-width: none;
        }
    }

    &__btn {
        min-width: 220px;

         @include mobile {
          width: 100%;
          min-width: auto;
        }
    }
}