@import "../mixins";

.instagram {
padding: var(--big-offset);

 &__container {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;

        @include small-tablet {
            justify-content: center;
        }
 }

  &__left {
      width: 380px;
      flex-shrink: 0;
      margin-right: var(--small-offset);
      background-color: var(--color-eastbay);
      border-radius: 4px;

      @include mobile {
      width: 100%;
      margin-right: 0;
      }
  }

  &__right {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: calc(100% - (380px + var(--small-offset)));

    @include small-tablet {
        display: none;
    }
  }

  &__title {
      margin-bottom: var(--second-offset);
      color: var(--color-white);
  }

  &__descr {
      margin: 0 auto;
      max-width: 260px;
      margin-bottom: var(--main-offset);
      font: var(--middle-book);
      color: var(--color-white);
      text-align: center;
  }

  &__input {
    margin-bottom: var(--small-offset);
}

&__link {
    display: inline-block;
    margin-bottom: var(--second-offset);
}

&__btn {
    width: 100%;
}

&__content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 160px);
    gap: 15px;
}

&__item {
    overflow: hidden;
    border-radius: 4px;
    a {
        display: block;
        width: 100%;
        height: 1005px;
    }

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &:nth-child(3){
      grid-column: 2 span;
      grid-row: 2 span;
    }

    &:nth-child(2){
        grid-row: 2;
    }
  }

  @include mobile {
    padding: 40px 0;
  }

}

.callback-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: var(--main-offset) var(--second-offset);

   
}