@import "../mixins";

.hero {
    position: relative;
    padding: 130px 0;
    height: 528px;
    background-color: var(--color-eastbay);

    &__container {
        position: relative;
        z-index: 5;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        height: 100%;
        

    }

    &__bg-text {
        font-family: "Intro", sans-serif;
        position: absolute;
        z-index: 0;
        margin-top: 70px;
        margin-left: 50px;
        left: 50%;
        top: 50%;
        font-weight: 700;
        font-size: 200px;
        line-height: 200px;
        color: rgba(255, 255, 255, 0.12);
        transform: translate(-50%, -50%);

        @include tablet {
            display: none;
        }
    
    }

    
        

    &__title {
        margin: 0;
        margin-top: 50px;
        margin-bottom: var(--second-offset);
        max-width: 50%;
        font:  700 36px / 140% var(--font-family);
        color: var(--color-white);

        @include small-tablet {
        max-width: none;
        }
    }

    &__description {
        margin: 0;
        margin-bottom: var(--main-offset);
        max-width: calc(50% - 100px);
        color: var(--middle-book);
        color: var(--color-white);

        @include small-tablet {
        max-width: none;
        }
    }

    @include small-tablet {
        padding: 50px 0;
        height: auto;
    }
}