@import "../mixins";

.about {
    position: relative;
    overflow: hidden;
    background-color: var(--color-eastbay);
    height: 580px;

    &__container {
        display: flex;
        position: relative;
        align-items: center;
        height: 100%;
    }

    &__figure {
       position: absolute;
        left: 0;
        top: 0;
    }

    &__text {
        width: 34%;
        transform: translateY(70px);

        @include small-tablet {
            width: 100%;
            transform: none;
        }
    }

    &__title {
        margin-bottom: var(--second-offset);
        color: var(--color-white);
    }

    &__descr {
     
        font: var(--middle-book);
        color: var(--color-white);
    }

    &__quote {
        font: inherit;
        margin-bottom: var(--second-offset);
        display: inline-block;
        &::before,
        &::after {
            display: none;
        }
    }

    &__author {
       
        display: flex;
        justify-content: flex-end;
        align-items: center;
        font: var(--prehigh-regular);
        font-style: normal;
        transform: translateX(50px);

        &::before{
            content: "";
            display: inline-block;
            width: 30px;
            height: 3px;
            margin-right: var(--second-offset);
            background-color: var(--color-white);
        }

        @include small-tablet {
           
            transform: none;
        }
    }



   
    &__image {
        position: absolute;
        right: 100px;
        top: -150px;
        flex-shrink: 0;
        width: 500px;
        height: 950px;
        overflow: hidden;
        transform: rotate(15deg);
       background-color: red;


       img {
        position: absolute;
        top: 0;
        max-width: none;
        left: 50%;
        width: 200%;
        height: 100%;
        object-fit: cover;
        transform: rotate(-15deg) translateX(-50%);
    }
      
    @include tablet {
          right: -100px;
      }

      @include small-tablet {
          display: none;
      }
   }
   @include small-tablet {
    padding: 50px 0;
    height: auto;
  }
   
}