@import "../mixins";

.footer {
    padding: 28px 0;
    background-color: var(--color-main);

    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include tablet {
            flex-direction: column;
            align-items: center;
        }
    }

    &__nav {
        .nav__list {
          @include small-tablet {
              flex-wrap: wrap;
          }
        }

        .nav__item {
          @include small-tablet {
              margin-bottom: var(--small-offset);
          }
        }
    }

    &__left {
        @include tablet {
          margin-bottom: var(--second-offset);
        }
    }
}