@import "../mixins";

.contacts {
    padding: var(--big-offset) 0;
    background-color: #f3f6f6;

    &__container {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;

        @include tablet {
            flex-wrap: wrap;
        }
    }

    &__title {
        display: flex;
        align-items: center;
        margin-bottom: var(--big-offset);
    }

    &__item {
        margin-bottom: var(--main-offset);
        font-style: normal;
    }

    &__subtitle {   
        margin: 0;
        margin-bottom: var(--main-offset);
        font: var(--small-book);
        text-transform: uppercase;
        color: var(--color-gray);
    }

    &__phone {
        outline: none;
        display: inline-block;
        margin-bottom: var(--small-offset);
        font: var(--middle-high-bold);
        color: var(--color-main);
        transition: color 0.3s ease-in-out;

        &:focus {
            color: var(--color-accent);
        }

        @include desktop {
            &:hover {
                color: var(--color-accent);
            }
        }

        &:active {
            color: var(--color-accent-hover);
        }
    }

    &__text {
        margin-right: var(--second-offset);
        width: calc (100% - (680px + var(--second-offset)));

        @include tablet {
            margin-right: 0;
            margin-bottom: var(--second-offset);
            width: 100%;
        }
    }

    &__address {
        display: block;

        @include mobile {
            font-size: 14px;
        }
    }

    &__map {
        width: 680px;
        height: 500px;

        @include tablet {
            width: 100%;
        }
    }
}