@import "../mixins";

.team {
    position: relative;
    padding: var(--big-offset) 0;
    background-color: var(--color-eastbay);

    &__title {
        color: var(--color-white);
    }

    &__item  {
        grid-column: 4 span;
    }

    &__list {
        position: relative;
        z-index: 5;
        gap: var(--main-offset) var(--second-offset);
    }

    &__figure {
        position: absolute;
        right: 0;
        top: 0;


        @include small-tablet {
            display: none;
        }
    }

    &__img {
        overflow: hidden;
        margin-bottom: var(--second-offset);
        border-radius: 4px;

        @include small-tablet {
            width: 100%;
        }
    }
    &__subtitle {
        display: block;
        margin-bottom: var(--second-offset);
        font: var(--prehigh-book);
        color: var(--color-white);
    }

    &__post {
        display: block;
        font: var(--middle-book);
        color: var(--color-white);
    }
}