@import "../mixins";

.modal {
    @include tablet {
     &::before {
       display: none;
      }
    }
}

.prod-modal {
    border-radius: 4px;
    padding: var(--main-offset);
    width: 1140px;

    .modal__close {
        @include small-tablet {
            z-index: 100;
        }
    }

    &__top {
        display: flex;
        justify-content: space-between;
        margin-bottom: var(--main-offset);

        @include small-tablet {
            flex-direction: column;
        }
    }
    &__bottom {
    margin-bottom: var(--main-offset);  
    }

    &__video {
        width: 100%;
        height: 450px;
        iframe {
            display: block;
            border: none;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        
    }

    @include tablet {
        margin: 0;
        width: 100%;
        
        
    }
}

.modal-slider {
    margin-right: var(--second-offset);
    width: calc(50% - (var(--second-offset) / 2));

    &__container {
        width: 100%;
        margin-bottom: var(--second-offset);
        overflow: hidden;
        font-size: 0;
        img {

            width: 100%;
            max-height: 460px;
            object-fit: cover;
        }
    }

    @include small-tablet {
       margin-right: 0;
       margin-bottom: 30px;
       width: 100%;
    }
}

.modal-info  {
display: flex;
flex-direction: column;
align-items: flex-start;
 padding-right: var(--big-offset);
 width: calc(50% - (var(--second-offset) / 2));

  &__top {
    display: flex;
    align-items: center;
    margin-bottom: var(--main-offset);
    font: var(--middle-book);
    line-height: 16px;
    color: var(--color-gray);

    @include mobile {
        flex-direction: column;
    }
  }
  &__vendor {
      margin-right: var(--main-offset);

      @include tablet {
       margin-right: 10px;
        
    }

    @include mobile {
        margin-right: 0;
        margin-bottom: 10px;
    }
  }

  &__quantity {
      span {
          color: var(--color-main);
      }
  }

  &__title {
      margin: 0;
      margin-bottom: var(--second-offset);
      font: var(--prehigh-book);
      line-height: 140%;
      color: var(--color-main);
  }

  &__rate {
    margin-bottom: var(--second-offset); 
  }

  &__subtitle {
    display: inline-block;
    font: var(--middle-book);
    line-height: 16px;
    color: var(--color-main);
    margin-bottom: var(--second-offset); 
  }

  

  &__price {
      display: flex;
      align-items: center;
  }

  &__current-price {
      margin-right: 32px;
      font: var(--middle-high-bold);
      line-height: 30px;
      color: var(--color-main);
  }

  &__old-price {
      font: var(--middle-book);
      line-height: 16px;
      color: var(--color-gray);
  }

  &__order {
      margin-top: auto;
      width: 100%;

      @include small-tablet {
          margin-top: 30px;
      }
  }

  @include small-tablet {
    margin: 0;
    width: 100%;
 }
}

.modal-sizes {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    &__item {
        margin-bottom: var(--second-offset);
        &:not(:last-child){
            margin-right: var(--second-offset);
        }
    }

    &__btn {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        border: 1px solid var(--color-gray);    
        padding: 6px 0px;
        border-radius: 4px;
        width: 70px;
        font: var(--small-book);
        color: var(--color-main);
    }
}

.modal-preview {
    display: grid;
    grid-template-columns: repeat(6, 70px);
    gap: var(--second-offset);

    &__item {
        border-radius: 4px;
        height: 75px;
        cursor: pointer;
        transition: opacity 0.3s;

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        @include desktop {
          &:hover {
            opacity: 0.7;
         }
        }

        

        &--active {
            opacity: 0.7;
            pointer-events: none;
        }
    }
}

.modal-note {
    margin-top: var(--second-offset);

    &__item {
        padding-left: 25px;
        font: var(--small-book);
        color: var(--color-gray);
        background-image: url("../img/check-list.svg");
        background-position: left center;
        background-size: 15px 12px;
        background-repeat: no-repeat;
        &:not(:last-child){
            margin-bottom: var(--small-offset);
        }
    }

}

.prod-bottom {
    display: flex;
    

    &__column {
    width: calc(50% - (var(--second-offset) / 2));

     &:first-child {
        margin-right: var(--second-offset);
     }

     @include small-tablet {
        width: 100%;

        &:first-child {
            margin-right: 0;
            margin-bottom: 30px;
         }
      }
    }
    &__title {
        margin: 0;
        margin-bottom: var(--second-offset);
        font: var(--prehigh-book);
        color: var(--color-main);
    }

    &__descr {
        margin: 0;
        max-width: 450px;
        font: var(--middle-book);
        color: var(--color-main);
    }

    @include small-tablet {
       display: block;
    }

}